<template>
    <CommonPage
        :editRules="editRules"
        :addRules="addRules"
        :otherVariable="otherVariable"
        :ajaxConfig="ajaxConfig"
        :propData="propData">
    </CommonPage>
</template>
<script>
    const pageName = '';
    import CommonPage from '$src/components/CommonPage'
    export default {
        components: {
            CommonPage
        },
        data() {
            return {
                otherVariable: {

                    detailTitle: `${pageName}`,
                    addTitle: `${pageName}`,
                    isMulSelect: false
                },
                editRules: {

                    'fname':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'dataState':[
                        { required: true, message: '', trigger: 'change' }
                    ]
                },
                addRules: {
                    'fcode':[
                        { required: true, message: '', trigger: 'blur' }
                    ],

                    'fname':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'tel':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'adress':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'dataState':[
                        { required: true, message: '', trigger: 'change' }
                    ]
                },
                ajaxConfig: {
                    dataObj: {
                        url: '/basedata/Mdm/Bank/selectByQueryModel',
                        method: 'fetch'
                    },
                    addObj: {
                        url: '/basedata/Mdm/Bank/addBank',
                        method: 'fetch'
                    },
                    updateObj: {
                        url: '/basedata/Mdm/Bank/updateBank',
                        method: 'fetch'
                    },
                    deleteObj: {
                        url: '/basedata/Mdm/Bank/deleteBank',
                        method: 'fetch'
                    }
                },
                propData: [
                    {
                        'name':'fid',
                        'prop':'fid',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':true,

                    },
                    {
                        'name':'',
                        'prop':'fcode',
                        'isRead':true,
                        'type':'normal',
                        'isMainSearch':true,
                        'isMain':false,
                        'selectList': this.getStaticList('whetherUse'),
                        'placeholder':'',

                    },
                    {
                        'name':'',
                        'prop':'fname',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,

                    },
                    {
                        'name':'',
                        'prop':'tel',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,

                    },

                    {
                        'name':'',
                        'prop':'dataState',
                        'type':'select',
                        'isMainSearch':false,
                        'isMain':false,
                        'selectList': this.getStaticList('whetherUse'),

                    },
                    {
                        'name':'',
                        'prop':'adress',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,

                    },
                    {
                        'name':'',
                        'prop':'remark',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,
                    }
                ]
            }
        }
    }
</script>



